@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;0,700;0,900;1,600&display=swap");
body {
  font-family: "Poppins", sans-serif;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  -webkit-tap-highlight-color: transparent;
}

/* width */
html::-webkit-scrollbar {
  width: 12px;
}

/* Track */
html::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
html::-webkit-scrollbar-thumb {
  background: #595eb0;
}

.dark-scroll-background {
  background: black;
}

/* Handle on hover */
html::-webkit-scrollbar-thumb:hover {
  opacity: 0.8;
}

.main-text {
  font-size: 1.5rem;
  transition: all 400ms ease-in-out;
}

.main-text:hover {
  text-shadow: 4px 4px 2px #32368c85;
}

@media (min-width: 375px) {
  .main-text {
    font-size: 1.7rem;
  }
}

@media (min-width: 425px) {
  .main-text {
    font-size: 2rem;
  }
}

@media (min-width: 615px) {
  .main-text {
    font-size: 3rem;
  }
}

@media (min-width: 950px) {
  .main-text {
    font-size: 4rem;
  }
}

@media (min-width: 1020px) {
  .main-text {
    font-size: 3.7rem;
  }
}

@media (min-width: 1376px) {
  .main-text {
    font-size: 5rem;
  }
}

.text-linear {
  background: -webkit-linear-gradient(#9499ff, #767ce8);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.btn {
  color: #9499ff;
  outline: 2px solid #9499ff;
  padding: 0.5rem 1.2rem;
  border-radius: 8px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  transition: all 500ms ease-in-out;
}

.btn:hover {
  background-color: #595eb0;
  background-position: left center; /* change the direction of the change here */
  outline: none;
  color: #e3e3e3;
}

@media (min-width: 600px) {
  .main {
    font-size: 3.5rem;
  }
}

@media (min-width: 1100px) {
  .main {
    font-size: 4.9rem;
  }
}

.project-title {
  letter-spacing: 0.5px;
}
.project-title:hover {
  background-image: linear-gradient(
    to right,
    #9499ff,
    #767ce8 50%,
    #1e1e1e 50%
  );
  background-size: 200% 100%;
  background-position: -100%;
  background-clip: text;
  -webkit-background-clip: text;

  -webkit-text-fill-color: transparent;
}

.project-title:before {
  content: "";
  background: #7b7ecc;
  display: block;
  transition: all 0.3s ease-in-out;
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 0;
  height: 3px;
}

.project-title:hover {
  background-position: 0;
}

.project-title:hover::before {
  width: 100%;
}

.project-title {
  transition: all 0.1s ease-in-out;
}

.project-img {
  transition: all 0.4s ease-in-out;
  filter: brightness(85%);
  outline: solid 1.2px #9499ff;
}

.project-img:hover {
  filter: brightness(40%);
  transform: scale(1.05);
  outline: solid 1.6px #9499ff;
}

.preview-link {
  box-shadow: inset 0 0 0 0 #51549c;
  color: #9499ff;
  margin: 0 -0.25rem;
  padding: 0 0.25rem;
  transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}
.preview-link:hover {
  box-shadow: inset 300px 0 0 0 #535590;
  color: #e3e3e3;
}

.modal-scrollbar {
  overflow-y: scroll;
}

.modal-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-scroll::-webkit-scrollbar {
  display: none;
}

.ahover {
  transition: all 400ms ease-in-out;
}

.ahover:hover {
  color: #9499ff !important;
}

.hoverscale {
  transition: all 500ms ease-in-out !important;
}
.hoverscale:hover {
  scale: 1.05 !important;
  filter: brightness(80%);
}

.no-scroll {
  overflow-y: hidden;
}

.hoverscale:hover div h2 {
  color: #9499ff;
  text-decoration: underline;
}

.three-dots {
  --color-primary: #5192ed;
  --color-secondary: #69a1f0;
  --color-tertiary: #7eaef2;
  --color-quaternary: #90baf5;
  --color-quinary: #a2c4f5;
  text-shadow: 3px 3px 0 var(--color-secondary), 6px 6px 0 var(--color-tertiary),
    9px 9px var(--color-quaternary), 12px 12px 0 var(--color-quinary);
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
  color: var(--color-primary);
  animation: shadows 1.2s ease-in infinite;
  letter-spacing: 0.4rem;
  border-radius: 50%;
}

@keyframes shadows {
  0% {
    text-shadow: none;
  }
  10% {
    transform: translate(-3px, -3px);
    text-shadow: 3px 3px 0 var(--color-secondary);
  }
  20% {
    transform: translate(-6px, -6px);
    text-shadow: 3px 3px 0 var(--color-secondary),
      6px 6px 0 var(--color-tertiary);
  }
  30% {
    transform: translate(-9px, -9px);
    text-shadow: 3px 3px 0 var(--color-secondary),
      6px 6px 0 var(--color-tertiary), 9px 9px var(--color-quaternary);
  }
  40% {
    transform: translate(-12px, -12px);
    text-shadow: 3px 3px 0 var(--color-secondary),
      6px 6px 0 var(--color-tertiary), 9px 9px var(--color-quaternary),
      12px 12px 0 var(--color-quinary);
  }
  50% {
    transform: translate(-12px, -12px);
    text-shadow: 3px 3px 0 var(--color-secondary),
      6px 6px 0 var(--color-tertiary), 9px 9px var(--color-quaternary),
      12px 12px 0 var(--color-quinary);
  }
  60% {
    text-shadow: 3px 3px 0 var(--color-secondary),
      6px 6px 0 var(--color-tertiary), 9px 9px var(--color-quaternary),
      12px 12px 0 var(--color-quinary);
  }
  70% {
    text-shadow: 3px 3px 0 var(--color-secondary),
      6px 6px 0 var(--color-tertiary), 9px 9px var(--color-quaternary);
  }
  80% {
    text-shadow: 3px 3px 0 var(--color-secondary),
      6px 6px 0 var(--color-tertiary);
  }
  90% {
    text-shadow: 3px 3px 0 var(--color-secondary);
  }
  100% {
    text-shadow: none;
  }
}

.planet {
  background-image: url("images/planet.svg");

  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  padding: 5rem;
  opacity: 0.2;
  animation: animatePlanet 30s infinite ease-in-out;
}

@keyframes animatePlanet {
  10% {
    transform: translateX(800px);
  }

  20% {
    transform: translateX(-400px);
  }

  30% {
    transform: translateX(-400px);
  }

  40% {
    transform: translateY(400px);
  }

  50% {
    transform: translateX(800px);
  }

  60% {
    transform: translateY(-600px);
  }

  70% {
    transform: translateY(100px);
  }

  80% {
    transform: translate(400px, 600px);
  }

  90% {
    transform: translate(900px, -300px);
  }
}


.arrow-animate{
animation:up-down 2s infinite ease-in-out;
}

@keyframes up-down {
  50%{
    rotate: 26deg;
    transform:translateY(-3px);

  }

  100%{
    rotate:26deg;
    transform: translateY(0px);
  }
}